
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";
import Home from './Components/Home/index'
import Quote from './Components/Quote/index'
import Voting from './Components/Voting/index'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Bmi from './Components/BMI/index'
import Students from './Components/Students/Students'
import Chuck from "./Components/Chuck/Chuck";

function RouterCall() {
    return( 
    <>
        <Router>

        
            
        <Navbar id="navbar"> 
        <Navbar.Brand className='text-white'><Link to="protfolio/" className='text-white text-decoration-none'>My Projects</Link></Navbar.Brand>
        <Nav className="mr-auto">
      <Nav.Link ><Link to="protfolio/">Home</Link></Nav.Link>
      <Nav.Link ><Link to="protfolio//Voting">Voting</Link></Nav.Link>
      <Nav.Link><Link to="/protfolio/Quotes">Quotes</Link></Nav.Link>
      <Nav.Link><Link to="protfolio//Students">Students</Link></Nav.Link>
      <Nav.Link><Link to="protfolio//Bmi">Bmi</Link></Nav.Link>
      <Nav.Link><Link to="protfolio//Chuck">Chuck</Link></Nav.Link>
    </Nav>
          </Navbar>
        
        

        <Switch>
          <Route path="protfolio/Voting">
            <Voting />
          </Route>
          <Route path="protfolio/Quotes">
        <Quote />
          </Route>
          <Route path="protfolio/Students">
            <Students />
          </Route>
          <Route path="protfolio/Bmi">
            <Bmi />
          </Route>
          <Route path="protfolio/Chuck">
            <Chuck />
          </Route>
          <Route path="protfolio/">
            <Home />
          </Route>
        </Switch>
       

    </Router>

    </>
  )
    }

export default RouterCall